* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  height: 100vh;
  gap: 1rem;
  flex-direction: column;
}

img {
  display: block;
  max-width: 100%;
}
button {
  background-color: #ffffff;
  color: #212121;
  width: 7rem;
  height: 3rem;
  font-size: 1rem;
  border-radius: 1rem;
}
button:hover,
button:disabled {
  cursor: pointer;
  background-color: transparent;
}
.timer {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.field {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
}
.label {
  font-size: 12px;
}

.field:not(:last-child) {
  margin-right: 40px;
}

.description {
  display: flex;
  margin-left: 60px;
  font-size: 12px;
}

.bold {
  font-size: 18px;
  font-weight: 600;
}